<template>
<div>
<el-form style="margin-top:20px;">
<el-row style="margin-left:20px;">
    <el-col :span="12">
        <el-form-item label="">
            <el-input v-model="input_string" placeholder="" style="width: 420px;">
            </el-input>
        </el-form-item>
    </el-col>
    <el-col :span="8" style="text-align: right;padding-right:50px;">
        <el-button-group>
            <el-button type="primary" icon="el-icon-search" @click="querykaoqing()"></el-button>
            <el-button type="primary" icon="el-icon-tickets" @click="getkaoqing()"></el-button>
        </el-button-group>
    </el-col>
</el-row>
</el-form>
<el-table :data="tableData"  border style="width: 50%;margin-left:20px" size="mini">
        <el-table-column prop="param2__id_curricula__name" label="" width="180" >
        </el-table-column>
        <el-table-column prop="param2__timebegin" label="" width="180" >
        </el-table-column>
        <el-table-column prop="param2__timeend" label="" width="180">
        </el-table-column>
        <el-table-column prop="attendrates" label="" width="180">
        </el-table-column>
</el-table>
<el-row  style="text-align: left;margin-left:160px">
    <el-pagination         
	      @size-change="handleSizeChange"
	      @current-change="handleCurrentChange"
	      :current-page.sync="currentpage"
        :page-size="pagesize"
	      layout="total,prev, pager, next, jumper"
	      :total="total">       
    </el-pagination>
</el-row>
</div>
</template>

<style>
  .el-table .warning-row {
    background: rgb(212, 58, 31);
  }

  .el-table .success-row {
    background: #5aee0b;
  }
</style>

<script>
import axios from 'axios';
  export default {
    data() {
      return {
      baseURL:"http://localhost:9000/polls/",
      id:'',
      tableData:[],
      input_string:'', //
      total:0,
      currentpage:1,  //
      pagesize:10, //10
      limits:10,
      }
    },
    mounted(){
    //
    this.getkaoqing();
    },
    methods:{
      getkaoqing(){
        this.id = this.$route.query.index;
         if(!this.$route.query.index){
           this.$router.push({path:'kechengs/'})
         }
        console.log(this.id);
        let that = this;
        axios
        .get(
          that.baseURL + 'StudentAttendance/',{params:{course_plan_id:that.id,page:that.currentpage,limits:that.limits}})
        .then(function(res){
          if(res.data.code == 0){
              that.$router.push('/index');
          }else{
              if(res.data.error_code == 0){
                    that.tableData = res.data.data;
                    that.$message({
                        message:'',
                        type:'success'
                    });
                }else {
                    //
                    that.$message.error(res.data.message);
                }
          }
          })
        .catch(function(err){
        that.$message.error("");
        })
      },
      //
        querykaoqing(){
            //Ajax--POST-->input_string
            let that = this;
            //Ajax
            axios
            .post(
                that.baseURL + 'StudentAttendance/',
                {
                    input_string: that.input_string
                }
            )
            .then(function(res){
                if(res.data.error_code == 0){
                    //shebei
                        that.tableData = res.data.data;
                    //
                        that.total = res.data.total_number*that.limits;
                    //
                    that.$message({
                        message:'',
                        type:'success'
                    });
                }else {
                    //
                    that.$message.error(res.data.message);
                }
            })
            .catch(function(err){
                //console.log(err);
                that.$message.error("");
            })
        },
        //
        handleSizeChange(size){
             //
             this.pagesize = size;
            //this.currentpage = 1;
             //
             this.getkaoqing();
         },
        //
        handleCurrentChange(pageNumber){
            //
            this.currentpage = pageNumber;
            //
            this.getkaoqing();
        },
    }
  }
</script>



